<div class="hello"><p v-if="isInApp">{{$t('login.isapp')}}</p><p v-show="isNew">{{ $t('profile.announce1') }}<router-link target="_blank" to="/privacy-policy">{{ $t('profile.announce2') }}</router-link>{{ $t('profile.announce3') }}</p><div class="ui divider" v-if="isNew"></div><br v-if="isNew"><br v-if="isNew"><br v-if="isNew"><div class="ui grid container"><div class="ui stackable two column row" v-if="!editing"><div class="ten wide column" v-show="!isNew"><div class="ui fluid card container" v-show="!isNew &amp;&amp; !editing"><card :h="root" :full="true" :book="book" @locate="locate" @addBook="addBook" @loginGoogle="loginGoogle" :uid="uid"></card></div></div><div class="filler" v-if="isNew"></div><div class="six wide column"><div class="ui massive green button" v-show="uid &amp;&amp; !editing" @click="startEdit"><i class="edit icon"></i>{{$t('profile.push')}}<span v-if="isNew">{{ $t('profile.start') }}</span><span v-else>{{ $t('profile.edit') }}</span></div></div><div class="filler" v-if="isNew"></div></div></div><div class="ui container" v-if="editing"><div class="ui warning message" v-if="longTimeNoSee() &gt; 0.25 &amp;&amp; !isNew"><div class="header">{{ $t('profile.update1')}}</div><span v-if="longTimeNoSee() &gt; 0.25">{{ $t('profile.update2') }}</span><!-- span(v-else-if="longTimeNoSee() == 1") 您的資料已一年未更新，目前已被系統隱藏，請立即更新即可恢復顯示。--></div><form class="ui form error warning success" id="main-form" v-show="editing"><h2 class="ui header">{{ $t('profile.fillform') }}</h2><div class="sub header">{{ $t('profile.form1')}}<br>{{ $t('profile.form2')}}<i class="red star"></i>{{ $t('profile.form3') }}</div><div class="ui vertical buttons"><button class="ui basic purple button" @click.prevent="$refs.fileInput.click()" v-show="!fileUploaded"><i class="file icon"></i>匯入自學2.0格式資料</button><input type="file" ref="fileInput" @change="handleFileChange" style="display: none"><button class="ui purple button" v-show="fileUploaded" @click.prevent="importData()" :class="{disabled: !fi