;&amp; root.note.length &lt; 20"><div class="header">{{$t('profile.form63')}}{{ 20 - root.note.length }}{{$t('profile.form64')}}</div><p>{{$t('profile.form65')}}</p></div><div class="ui success message" v-if="root.note &amp;&amp; root.note.length &gt;= 20"><div class="header">{{$t('profile.form66')}}</div><p>{{$t('profile.form67')}}</p></div></div><div class="warning" v-if="!emailVerified &amp;&amp; root.login_method === 'email'">note: 您的Email尚未驗證<button class="ui button" @click="$emit('resendVerificationEmail')">按此重發驗證信</button></div><div class="warning" v-if="!isValid(root)">note: {{$t('profile.form68')}}<i class="red star"></i>{{$t('profile.form69')}}</div></form><p v-if="isNew"> <input class="ui checkbox" type="checkbox" v-model="agree">{{$t('profile.form70')}}<router-link target="_blank" to="/privacy-policy">{{$t('profile.form71')}}</router-link></p><div class="ui vertical buttons"><a class="ui large blue button" :class="{disabled: !isValid(root) || (!emailVerified &amp;&amp; root.login_method === 'email')}" @click="updateFlag"><span v-if="isNew"><i class="flag icon"></i>{{$t('login.raise_flag')}}</span><span v-else><i class="flag icon"></i>{{$t('login.update_flag')}}</span></a><router-link class="ui large green button" v-if="!isNew" :to="'/flag/' + uid"><i class="user icon"></i>{{$t('login.check_flag')}}<i class="chevron right icon"></i></router-link><div class="ui large red button" v-if="!isNew" @click="confirmDelete"><i class="delete icon"></i>{{$t('login.delete_flag')}}</div></div><br><br><br><br></div></div>